@font-face {
	font-family: 'Montserrat';
	font-weight: 500;
	font-style: normal;
	font-display: swap;
	src: url(../fonts/MyFont-Regular.ttf) format('truetype');
}

@font-face {
	font-family: 'Montserrat';
	font-weight: 900;
	font-style: normal;
	font-display: swap;
	src: url(../fonts/Montserrat-Black.woff2) format('woff2'),
		url(../fonts/Montserrat-Black.woff) format('woff'), url(../fonts/Montserrat-Black.ttf) format('truetype');
}

@font-face {
	font-family: 'Montserrat';
	font-weight: 700;
	font-style: italic;
	font-display: swap;
	src: url(../fonts/Montserrat-BoldItalic.woff2) format('woff2'),
		url(../fonts/Montserrat-BoldItalic.woff) format('woff'), url(../fonts/Montserrat-BoldItalic.ttf) format('truetype');
}
